.scroll-icon { position: absolute; bottom: 30px; left: 50%; margin-left: calc(-27px / 2); animation: mouse 0.5s infinite linear; background-image: url("../img/icons.png"); width: 27px; height: 41px; background-position: -138px 0px; }
